// src/app/empresas/[seo_url]/page.tsx
"use client"

import { useState } from "react"
import TabNavigation from "@/components/empresa/TabNavigation"
import AboutTab from "@/components/empresa/tabs/AboutTab"
import ServicesTab from "@/components/empresa/tabs/ServicesTab"
import PortfolioTab from "@/components/empresa/tabs/PortfolioTab"
import ReviewsTab from "@/components/empresa/tabs/ReviewsTab"
import FaqTab from "@/components/empresa/tabs/FaqTab"
import ContactTab from "@/components/empresa/tabs/ContactTab"

interface EmpresaPageProps {
  params: {
    seo_url: string
  }
}

export default function EmpresaPage({ params }: EmpresaPageProps) {
  const [activeTab, setActiveTab] = useState<
    "about" | "services" | "portfolio" | "reviews" | "faq" | "contact"
  >("about")

  return (
    <div className="font-sans bg-gray-50 text-gray-800">
      {/* TODO: extrair para <Header seoUrl={params.seo_url} /> */}
      <header className="relative">
        {/* …seu markup do banner… */}
      </header>

      <main className="max-w-6xl mx-auto px-4 py-8">
        <TabNavigation activeTab={activeTab} onChange={setActiveTab} />

        <section className="mt-6">
          {activeTab === "about"    && <AboutTab seoUrl={params.seo_url} />}
          {activeTab === "services" && <ServicesTab seoUrl={params.seo_url} />}
          {activeTab === "portfolio"&& <PortfolioTab seoUrl={params.seo_url} />}
          {activeTab === "reviews"  && <ReviewsTab seoUrl={params.seo_url} />}
          {activeTab === "faq"      && <FaqTab seoUrl={params.seo_url} />}
          {activeTab === "contact"  && <ContactTab seoUrl={params.seo_url} />}
        </section>
      </main>
    </div>
  )
}
