import { FC } from 'react'
import classNames from 'classnames'

interface TabNavigationProps {
  activeTab: string
  onChange: (tab: string) => void
}

const tabs = [
  { id: 'about',    label: 'Sobre',       icon: 'fa-info-circle' },
  { id: 'services', label: 'Serviços',    icon: 'fa-solar-panel' },
  { id: 'portfolio',label: 'Portfólio',   icon: 'fa-images' },
  { id: 'reviews',  label: 'Avaliações',  icon: 'fa-star' },
  { id: 'faq',      label: 'FAQ',         icon: 'fa-question-circle' },
  { id: 'contact',  label: 'Contato',     icon: 'fa-envelope' },
]

const TabNavigation: FC<TabNavigationProps> = ({ activeTab, onChange }) => (
  <nav className="mb-8 border-b border-gray-200">
    <div className="flex overflow-x-auto">
      {tabs.map(tab => {
        const isActive = tab.id === activeTab
        return (
          <button
            key={tab.id}
            onClick={() => onChange(tab.id)}
            className={classNames(
              'tab-button px-6 py-4 font-medium transition-colors border-b-2 whitespace-nowrap',
              {
                'text-blue-600 border-blue-600': isActive,
                'text-gray-500 border-transparent hover:text-blue-600 hover:border-blue-200': !isActive
              }
            )}
          >
            <i className={`fas ${tab.icon} mr-2`} /> {tab.label}
          </button>
        )
      })}
    </div>
  </nav>
)

export default TabNavigation
